.form {
  &-input {
    padding: 18px 40px 18px 12px;
    border-radius: 6px;
    width: 100%;
    height: 56px;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: var(--base-black);
    appearance: none ;
    outline: none !important;
    border: none;
    border: 1px solid #E2E2E2;
    display: block;
    background-color: #F8F8F8;
    &::placeholder {
      font-weight: 400;
      font-size: 15px;
      line-height: 133%;
      letter-spacing: -0.24px;
      color: #92969D;
    }
    &:focus {
      border: 1px solid var(--cyan-400);
    }
    @include small-tablet {
      padding: 14px 40px 14px 12px;
      height: 44px;
      font-size: 15px;
    }
  }
  &-btn {
    border: none;
    background: none;
    outline: none !important;
    cursor: pointer;
    padding: 18px 36px;
    border-radius: 8px;
    height: 56px;
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 16px;
    line-height: 125%;
    color: var(--base-black);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--grey-200);
    transition: background-color 0.3s ease-in;
    &:hover {
      background-color: var(--cyan-400);
    }
    @include small-tablet {
      padding: 14px 28px;
      height: 44px;
      font-size: 15px;
    }
  }
}
