.tabs {
  &__nav {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
    display: flex;
    border-bottom: 1px solid var(--grey-200);
    overflow-x: auto;
    white-space: nowrap;
    scrollbar-width: none;
    /*mozilla*/
    &::-webkit-scrollbar {
      display: none;
      /*chrome*/
    }
  }

  &__nav-item:not(:last-child) {
    margin-right: 24px;
    @include mobile {
      margin-right: 16px;
    }
  }

  &__nav-btn {
    padding: 16px 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    color: #7D7D7D;
    background-color: transparent;
    border-bottom: 2px solid transparent;
    transition: color 0.5s ease, border-bottom 0.5s ease;

    &:hover {
      color: var(--cyan-400);
    }
  }

  &__panel {
    display: none;
  }

  &__panel--active {
    display: block;
  }

  &__nav-btn--active {
    color: var(--base-grey);
    border-bottom: 2px solid var(--cyan-400);

    &:hover {
      color: var(--base-grey);
    }
  }
}
