.main {
  padding-top: 72px;

  @include tablet {
    padding-top: 52px;
  }

  &__container {
    display: grid;
    grid-template-columns: 80% 20%;

    @media (max-width: 1300px) {
      grid-template-columns: 75% 25%;
    }
    @include tablet {
      grid-template-columns: repeat(1, 100%);
    }
  }

  .column__left {
    display: flex;
    flex-direction: column;
    flex: 0 1 1206px;
  }

  .column__right {
    display: flex;
    flex-direction: column;
    flex: 0 0 287px;
    margin-left: 20px;

    @include tablet {
      margin-left: 0;
    }
  }
}
