.vacancy {
  @include tablet {
    margin-bottom: 40px;
  }

  .cards {
    padding: 20px;
    border-radius: 8px;
    background-color: var(--base-white);
  }

  .card {
    padding-bottom: 20px;
    border-bottom: 1px solid #E5E5E5;
    &:not(:last-child) {
      margin-bottom: 20px;
    }

    a:hover {
      h3 {
        color: var(--cyan-400);
      }
    }
  }

  .title {
    font-weight: 600;
    font-size: 16px;
    color: var(--base-black);
    margin-bottom: 10px;
    transition: color 0.3s ease-in;
    @include mobile {
      margin-bottom: 6px;
    }
  }
  .city {
    font-weight: 600;
    font-size: 14px;
    color: var(--grey-500);
    margin-bottom: 10px;
    @include mobile {
      margin-bottom: 6px;
    }
  }
  .pay {
    font-weight: 500;
    font-size: 14px;
    color: var(--grey-500);
    margin-bottom: 10px;
    @include mobile {
      margin-bottom: 6px;
    }
    span {
      font-weight: 600;
      color: var(--base-black);
    }
  }
  .info {
    font-weight: 600;
    font-size: 14px;
    color: var(--base-black);
  }
  .btn {
    margin-top: 20px;
    width: 100%;
    text-align: center;

    a {
      width: 100%;
      text-align: center;
      justify-content: center;
    }
  }
}
