.events {
  margin-bottom: 40px;

  &__slider {
    margin: -10px -10px;
  }

  .slide {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__slide {
    overflow: hidden;
    padding: 20px;
    position: relative;
    margin: 10px 10px;
    border-radius: 8px;
    max-height: 100%;
    display: flex;
    flex-direction: column;

    &:hover {
      &::before {
        transform: scale(1.1);
      }
      &::after {
        opacity: 0.8;
      }
    }

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../img/events/events-img.jpg');
      background-size: cover;
      background-repeat: no-repeat;
      border-radius: 8px;
      z-index: -1;
      background-color: var(--grey-500);
      transition: transform 0.5s ease-in;
    }
    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 8px;
      z-index: -1;
      background-color: var(--base-black);
      opacity: 0.6;
      transition: opacity 0.4s ease-in;
    }
  }

  .date {
    color: var(--base-white);
    margin-bottom: 20px;
    @include small-tablet {
      margin-bottom: 12px;
    }
  }

  .title4 {
    min-height: 118px;
    display: block;
    display: -webkit-box;
    max-width: 284px;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    color: var(--base-white);
    margin-bottom: 20px;

    @include small-tablet {
      max-width: 400px;
      min-height: auto;
      margin-bottom: 16px;
      font-size: 22px;
    }
    @include mobile {
      font-size: 20px;
    }
    @media (max-width: 360px) {
      font-size: 18px;
    }
  }
  &__btn {
    margin-top: auto;
  }
}
