.news {
  width: 100%;
  padding: 20px;
  border-radius: 16px;
  background-color: #FFFFFF;
  margin-bottom: 40px;

  .slide img {
    border-top-left-radius: 7px;
    border-bottom-left-radius: 7px;
    display: block;
    width: 247px;
    max-height: 100%;
    object-fit: cover;

    @media (max-width: 1500px) {
      width: 180px;
    }

    @media (max-width: 1300px) {
      display: none;
    }
  }

  &__slider {
    margin: -10px -10px;
  }

  &__slide {
    margin: 10px 10px;
    display: flex;
    border: 1px solid var(--grey-200);
    border-radius: 8px;
    background-color: #FFFFFF;
  }

  &__content {
    padding: 20px;
    max-height: 100%;
    display: flex;
    flex-direction: column;

    .title3 {
      margin-bottom: 8px;
    }

    .date {
      margin-bottom: 12px;
      @include mobile {
        margin-bottom: 8px;
      }
    }

    .descr2 {
      margin-bottom: 20px;
      @include small-tablet {
        margin-bottom: 12px;
      }
      @include mobile {
        font-size: 15px;
      }
    }
    .image {
      height: 100%;
    }
  }
  &__btn {
    margin-top: auto;
  }
}
