.search {
  padding-bottom: 72px;
  @include tablet {
    padding-bottom: 52px;
  }
  h1 {
    font-weight: 700;
    font-size: 14px;
    line-height: 129%;
    letter-spacing: -0.15px;
    color: #7D7D7D;
    margin-bottom: 8px;
  }
  &__form {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    @media (max-width: 780px) {
      width: 100%;
    }
    @include mobile {
      margin-bottom: 30px;
      flex-direction: column;
    }
  }
  &__input {
    width: 594px;
    &::-webkit-search-cancel-button {
      appearance: none;
    }
    @media (max-width: 780px) {
      width: 100%;
    }
  }
  &__btn {
    margin-left: 20px;
    @media (max-width: 780px) {
      margin-left: 10px;
    }
    @include mobile {
      margin-left: 0;
      margin-top: 10px;
      width: 100%;
    }
  }
  .cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    row-gap: 10px;
    @media (max-width: 1100px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .card {
    padding: 18px 20px;
    border: 1px solid var(--grey-200);
    border-radius: 8px;
    background-color: var(--base-white);
    transition: background-color 0.3s ease-in;

    @include mobile {
      padding: 15px 16px;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: 400;
      font-size: 16px;
      @include mobile {
        font-size: 15px;
      }
      p {
        color: var(--base-black);
        text-decoration: underline;
        display: block;
        display: -webkit-box;
        max-width: 650px;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        @media (max-width: 1100px) {
          max-width: 900px;
        }
        @include mobile {
          max-width: 100%;
          -webkit-line-clamp: 20;
          overflow: visible;
        }
      }
      i {
        font-size: 24px;
        text-decoration: none !important;
        color: var(--grey-500);
        margin-left: 20px;
        transition: color 0.3s ease-in;
        &:hover {
          color: var(--base-grey);
        }
      }
    }
    &:hover {
      background-color: var(--grey-200);
    }
  }
}

.empty {
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: var(--base-black);
  @include medium-tablet {
    font-size: 18px;
  }
  @include small-tablet {
    font-size: 16px;
  }
  @include mobile {
    font-size: 15px;
  }
}

span.deleteicon {
  position: relative;
  display: inline-flex;
  align-items: center;
  @media (max-width: 780px) {
    flex: 1 1 100%;
  }
  @include mobile {
    width: 100%;
  }
}
span.deleteicon i {
  position: absolute;
  display: block;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  font-size: 24px;
  line-height: 40px;
  text-align: center;
  color: var(--grey-400);
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in, opacity 0.3s ease-in;
}
span.deleteicon input {
  box-sizing: border-box;
}
span.deleteicon input:focus-visible + i {
  visibility: visible;
  opacity: 1;
  transition: color 0.3s ease-in;
  &:hover {
    color: var(--base-black);
  }
}
