.page-vacancy {
  padding-bottom: 72px;
  @include tablet {
    padding-bottom: 52px;
  }
  .title2 {
    margin-bottom: 24px;
  }
  .row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include mobile {
      flex-direction: column;
    }
  }
  .sort {
    margin-top: 0;
    @include mobile {
      margin-bottom: 24px;
    }
  }
  .cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    row-gap: 10px;
    margin-bottom: 70px;
    @include medium-tablet {
      margin-bottom: 30px;
    }
    @include small-tablet {
      margin-bottom: 20px;
    }
    @include mobile {
      margin-bottom: 20px;
    }
  }
  .card {
    padding: 18px 20px;
    border: 1px solid #E5E7EB;
    border-radius: 8px;
    background-color: var(--base-white);
    cursor: pointer;
    transition: background-color 0.3s ease-in;
    @include mobile {
      padding: 16px;
    }
    &:hover {
      background-color: var(--grey-200);
    }
  }
  .card a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--base-black);
    font-weight: 400;
    font-size: 16px;
    line-height: var(--line-height);
    @include tablet {
      font-size: 14px;
    }
    @include small-tablet {
      font-size: 12px;
    }
    @include mobile {
      flex-direction: column;
      align-items: start;
      justify-content: flex-start;
      height: 100%;
      font-size: 14px;
    }
    .title {
      width: 20%;
      font-weight: 400;
      font-size: 16px;
      line-height: var(--line-height);
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-right: 20px;
      transition: color 0.3s ease-in;
      @include tablet {
        font-size: 14px;
      }
      @include small-tablet {
        font-size: 12px;
      }
      @include mobile {
        width: 100%;
        -webkit-line-clamp: inherit;
        margin-right: 0;
        margin-bottom: 10px;
        font-weight: 600;
        font-size: 16px;
        color: var(--base-black);
      }
    }
    .pay {
      width: 20%;
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-right: 20px;
      @include small-tablet {
        margin-right: 10px;
      }
      @include mobile {
        width: 100%;
        -webkit-line-clamp: inherit;
        margin-right: 0;
        margin-bottom: 6px;
      }
    }
    .info {
      width: 30%;
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-right: 20px;
      @include small-tablet {
        margin-right: 10px;
      }
      @include mobile {
        width: 100%;
        -webkit-line-clamp: inherit;
        margin-right: 0;
        margin-bottom: 6px;
      }
    }
    .experience {
      width: 20%;
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-right: 20px;
      @include small-tablet {
        margin-right: 10px;
      }
      @include mobile {
        width: 100%;
        -webkit-line-clamp: inherit;
        margin-right: 0;
        margin-bottom: 6px;
      }
    }
    i {
      width: 22px;
      height: 22px;
      font-size: 22px;
      color: var(--base-black);
      transition: color 0.3s ease-in;
      @include tablet {
        width: 18px;
        height: 18px;
        font-size: 18px;
      }
      @include mobile {
        color: var(--cyan-400);
        margin-left: auto;
      }
      &:hover {
        color: var(--base-grey);
      }
    }
  }
}
