.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  @include small-tablet {
    padding: 10px;
  }
  li {
    &:not(:last-child) {
      margin-right: 4px;
    }
  }
  a {
    padding: 9px 12px;
    min-width: 32px;
    display: inline-flex;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    color: #2E2E2E;
    transition: background-color 0.3s ease-in;
    &:hover {
      background-color: var(--cyan-200);
    }
    &:active {
      background-color: var(--cyan-400);
    }
  }
  .active {
    color: var(--base-black);
    background-color: var(--cyan-400);
  }
  .arrow {
    border-radius: 0;
    font-size: 18px;
    line-height: 32px;
    transition: color 0.3s ease-in;
    &:hover {
      background-color: transparent;
      color: var(--cyan-400);
    }
  }
}
