.birthday {
  margin-bottom: 40px;

  .title2 {
    margin-bottom: 40px;

    @media (max-width: 1200px) {
      margin-bottom: 20px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;

    @include tablet {
      grid-template-columns: repeat(3, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(2, 1fr);
      justify-content: center;
    }
    @media (max-width: 400px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__card {
    padding: 20px;
    border-radius: 16px;
    background-color: #FFFFFF;

    .image {
      width: 100%;
      border-radius: 8px;
      margin-bottom: 20px;

      @media (max-width: 1300px) {
        margin-bottom: 12px;
      }
      @media (max-width: 360px) {
        height: 240px;
      }
    }

    .title3 {
      margin-bottom: 8px;
      transition: color 0.3s ease-in;

      @media (max-width: 1300px) {
        font-size: 20px;
      }

      @media (max-width: 480px) {
        font-size: 18px;
        margin-bottom: 5px;
      }
    }

    .descr2 {
      color: var(--grey-500);
    }

    a:hover {
      .title3 {
        color: var(--cyan-400);
      }
    }
  }

  &__btn {
    margin-top: 20px;
    width: 100%;
    text-align: center;

    a {
      width: 100%;
      text-align: center;
      justify-content: center;
    }
  }
}
