.header {
  top: 0;
  left: 0;
  right: 0;
  padding-top: 16px;
  padding-bottom: 16px;
  z-index: 100;
  background-color: var(--base-black);
  @include medium-tablet {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__container {
    margin: 0 auto;
    margin-left: 85px;
    margin-right: 190px;
    padding: 0 var(--container-offset);
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1800px) {
      max-width: 1646px;
      margin: 0 auto;
    }
  }

  &__profile {
    display: flex;
    align-items: center;
    padding-left: 8px;

    img {
      padding: 2px;
      border-radius: 50%;
      margin-right: 10px;
      background: -moz-linear-gradient(-45deg, rgba(0, 0, 0, 0.6) 0%, rgba(29, 233, 182, 1) 24%, rgba(0, 0, 0, 1) 100%);
      background: -webkit-linear-gradient(-45deg, rgba(0, 0, 0, 0.6) 0%, rgba(29, 233, 182, 1) 24%, rgba(0, 0, 0, 1) 100%);
      background: linear-gradient(135deg, rgba(0, 0, 0, 0.6) 0%, rgba(29, 233, 182, 1) 24%, rgba(0, 0, 0, 1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#99000000', endColorstr='#000000', GradientType=1);
    }
    &:hover .header__name {
      color: var(--cyan-400);
    }
  }
  &__name {
    width: 116px;
    font-weight: 600;
    font-size: 12px;
    color: var(--base-white);
    transition: color 0.3s ease-in;
  }
  .logo {
    width: 172px;
    img {
      display: block;
    }
    @media (max-width: 1200px) {
      width: auto;
    }
    @include medium-tablet {
      img {
        width: 63px;
        height: 39px;
      }
    }
    @include mobile {
      img {
        width: 57px;
        height: 34px;
      }
    }
  }
  .nav {
    &__list {
      display: flex;
      align-items: center;
    }
    &__item:not(:last-child) {
      margin-right: 40px;
    }
    &__link {
      font-weight: 600;
      font-size: 16px;
      color: var(--base-white);
      padding: 0;
      transition: color 0.3s ease-in;
      &:hover {
        color: var(--cyan-400);
      }
      @media (max-width: 1100px) {
        font-size: 14px;
      }
    }
    .active {
      color: var(--cyan-400);
    }
  }
  @include medium-tablet {
    .nav {
      position: fixed;
      right: 0;
      top: 58px;
      z-index: 500;
      overflow-y: auto;
      margin: 0 auto;
      width: 334px;
      height: 100vh;
      background-color: var(--base-grey);
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
    }
    .nav__list {
      flex-direction: column;
      margin-top: 50px;
      width: 100%;
      height: 100%;
    }
    .nav__item:not(:last-child) {
      margin-right: 0;
      margin-bottom: 45px;
    }
    .nav__link {
      font-size: 20px;
    }
  }
  @media (max-width: 350px) {
    .nav {
      width: 320px;
    }
  }
  .burger {
    @include burger;
    z-index: 501;
    @media (min-width: 992px) {
      display: none;
    }
  }
  .menu--active {
    opacity: 1;
    visibility: visible;
  }
}
