html {
  box-sizing: border-box;
  height: 100%;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

::selection {
  background-color: var(--cyan-400);
  color: var(--base-white);
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-size: 16px;
  color: var(--base-black);
  line-height: var(--line-height);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  height: 100%;
  background-color: var(--grey-100);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}

.main {
  flex: 1 0 auto;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

p {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.title1 {
  font-weight: 700;
  font-size: 48px;
  color: var(--base-grey);
  line-height: var(--line-height);
  margin-bottom: 40px;
}
@media (max-width: 1024px) {
  .title1 {
    font-size: 42px;
    margin-bottom: 30px;
  }
}
@media (max-width: 768px) {
  .title1 {
    font-size: 36px;
  }
}
@media (max-width: 575.98px) {
  .title1 {
    font-size: 32px;
  }
}

.title2 {
  font-weight: 700;
  font-size: 24px;
  color: var(--base-grey);
  line-height: var(--line-height);
  margin-bottom: 20px;
}
@media (max-width: 400px) {
  .title2 {
    font-size: 20px;
  }
}

.title3 {
  font-weight: 500;
  font-size: 24px;
  color: var(--base-black);
  line-height: var(--line-height);
}
@media (max-width: 400px) {
  .title3 {
    font-size: 20px;
  }
}

.title4 {
  font-weight: 600;
  font-size: 24px;
  color: var(--base-black);
  line-height: var(--line-height);
}
@media (max-width: 400px) {
  .title4 {
    font-size: 20px;
  }
}

.descr2 {
  font-weight: 400;
  font-size: 16px;
  line-height: var(--line-height);
  color: var(--base-black);
}

.date {
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: var(--grey-500);
}
