.title1 {
  font-weight: 700;
  font-size: 48px;
  color: var(--base-grey);
  line-height: var(--line-height);
  margin-bottom: 40px;

  @include tablet {
    font-size: 42px;
    margin-bottom: 30px;
  }
  @include small-tablet {
    font-size: 36px;
  }
  @include mobile {
    font-size: 32px;
  }
}

.title2 {
  font-weight: 700;
  font-size: 24px;
  color: var(--base-grey);
  line-height: var(--line-height);
  margin-bottom: 20px;

  @media (max-width: 400px) {
    font-size: 20px;
  }
}

.title3 {
  font-weight: 500;
  font-size: 24px;
  color: var(--base-black);
  line-height: var(--line-height);

  @media (max-width: 400px) {
    font-size: 20px;
  }
}

.title4 {
  font-weight: 600;
  font-size: 24px;
  color: var(--base-black);
  line-height: var(--line-height);

  @media (max-width: 400px) {
    font-size: 20px;
  }
}

.descr2 {
  font-weight: 400;
  font-size: 16px;
  line-height: var(--line-height);
  color: var(--base-black);
}

.date {
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: var(--grey-500);
  @include mobile {
    font-size: 15px;
  }
}
