.footer {
  position: relative;
  flex: 0 0 auto;
  overflow: hidden;
  padding: 16px 0 12px;
  background: #171717;
  background: -moz-linear-gradient(left, #171717 45%, #1de9b6 100%);
  background: -webkit-linear-gradient(left, #171717 45%, #1de9b6 100%);
  background: linear-gradient(to right, #171717 45%, #1de9b6 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#171717', endColorstr='#1de9b6', GradientType=1);

  @include small-tablet {
    padding: 12px 0 10px;
  }
  @include mobile {
    padding: 7px 0 6px;
  }

  &::before {
    position: absolute;
    content: "";
    top: 50%;
    right: -110px;
    transform: translateY(-50%);
    width: 398px;
    height: 232px;
    background-image: url('../img/footer-decor.svg');
    background-repeat: no-repeat;
    background-size: contain;
    @include mobile {
      width: 255px;
      height: 148px;
      right: -50px;
      opacity: 0.5;
    }
    @media (max-width: 400px) {
      width: 189px;
      height: 110px;
      right: -50px;
      opacity: 0.3;
    }
  }

  &__logo {
    margin-left: 16px;
    @include medium-tablet {
      margin-left: 0;
      img {
        width: 80px;
      }
    }
    @include small-tablet {
      img {
        width: 64px;
      }
    }
    @include mobile {
      img {
        width: 50px;
      }
    }
  }
}
