.hi {
  margin-bottom: 40px;

  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__card {
    padding: 20px;
    border-radius: 16px;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: var(--base-white);
  }

  &__main {
    display: flex;
    align-items: start;
    margin-bottom: 20px;

    @include small-tablet {
      flex-direction: column;
      margin-bottom: 12px;
    }

    .image {
      min-width: 246px;
      max-width: 246px;
      height: 200px;
      border-radius: 8px;
      margin-right: 20px;
      @media (max-width: 1400px) {
        min-width: 170px;
        max-width: 170px;
        height: 200px;
      }
      @media (max-width: 1200px) {
        min-width: 130px;
        max-width: 130px;
        height: 160px;
      }
      @include small-tablet {
        width: 100%;
        max-width: 100%;
        height: 220px;
        margin-right: 0;
        margin-bottom: 20px;
      }
      @include mobile {
        height: 290px;
      }
      @media (max-width: 360px) {
        height: 240px;
      }
    }
  }

  &__name {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: var(--base-black);
    margin-bottom: 20px;

    @include medium-tablet {
      margin-bottom: 12px;
    }
  }

  &__group {
    &-title {
      font-weight: 600;
      font-size: 16px;
      line-height: 100%;
      color: var(--base-grey);
      margin-bottom: 8px;
      @include small-tablet {
        margin-bottom: 5px;
      }
    }
    &-text {
      font-weight: 500;
      font-size: 14px;
      color: var(--grey-500);
    }
    &:not(:last-child) {
      margin-bottom: 20px;

      @media (max-width: 1200px) {
        margin-bottom: 12px;
      }
      @include small-tablet {
        margin-bottom: 8px;
      }
    }
  }

  &__descr {
    font-weight: 500;
    font-size: 16px;
    color: var(--grey-500);
    margin-bottom: 20px;
    @include small-tablet {
      font-size: 14px;
    }
    @include mobile {
      font-size: 15px;
      margin-bottom: 16px;
    }
  }

  &__btn {
    margin-top: auto;
  }
}
