.breadcrumb {
  margin-bottom: 24px;
  @include mobile {
    margin-bottom: 12px;
  }
  p {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: var(--grey-400);
  }
  a {
    color: var(--grey-400);
    transition: color 0.3s ease-in;
    &:hover {
      color: var(--cyan-400);
    }
  }
  .active {
    color: var(--grey-700);
  }
}
