.about {
  padding-bottom: 72px;
  @include tablet {
    padding-bottom: 52px;
  }
  .title1 {
    @include small-tablet {
      margin-bottom: 24px;
    }
    @include mobile {
      margin-bottom: 16px;
    }
  }
  .content {
    p {
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      color: var(--base-black);

      @include small-tablet {
        font-size: 18px;
      }
      @include mobile {
        font-size: 16px;
      }
    }
  }
}
