.news, .events {
  .slick-slider {
    border-radius: 8px;
  }

  .slick-dotted.slick-slider {
    margin-bottom: 0 !important;
  }

  &-slider-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 40px auto 20px auto;

    @media (max-width: 1200px) {
      margin: 20px auto 10px auto;
    }

    @include mobile {
      margin: 10px auto 0px auto;
    }
  }

  .slider-btn {
    width: 24px;
    height: 24px;

    i {
      font-size: 24px;
      color: var(--base-black);
      line-height: 24px;
      transition: color 0.3s ease-in;
    }

    &:hover {
      i {
        color: var(--cyan-400);
      }
    }

    &:disabled {
      opacity: 0.2;

      :hover {
        color: var(--base-black);
      }
    }
  }

  .prev {
    margin-right: 4px;
  }

  .next {
    margin-left: 4px;
  }

  .slider__dots {
    position: relative;
  }

  &-slider__dots-list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &-slider__dots-list li {
    cursor: pointer;
    position: relative;
    z-index: 0;
    text-align: center;

    margin-left: 2px;
    margin-right: 2px;
    padding: 0 10px;
    min-width: 32px;
    height: 32px;
    font-weight: 500;
    font-size: 12px;
    line-height: 32px;
    color: var(--base-black);
    text-align: center;
    border-radius: 50%;
    opacity: 1;
    background-color: transparent;
    transition: background-color 0.3s ease-in-out;

    &:hover {
      background-color: var(--cyan-200);
    }
  }

  &-slider__dots-list li.slick-active {
    color: var(--base-black);
    background-color: var(--cyan-400);
  }
}
