.documents {
  padding-bottom: 72px;
  @include tablet {
    padding-bottom: 52px;
  }
  .empty {
    margin-top: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: var(--base-black);
    @include medium-tablet {
      font-size: 18px;
    }
    @include small-tablet {
      font-size: 16px;
    }
    @include mobile {
      font-size: 15px;
    }
  }
  .tabs {
    margin-bottom: 70px;
    @include medium-tablet {
      margin-bottom: 30px;
    }
    @include small-tablet {
      margin-bottom: 20px;
    }
    @include mobile {
      margin-bottom: 20px;
    }
  }
  .row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include mobile {
      flex-direction: column;
    }
  }
  .tabs__header {
    display: flex;
    align-items: end;
    justify-content: space-between;
    flex-wrap: wrap;
    @include medium-tablet {
      flex-direction: column;
      align-items: start;
      display: block;
    }
  }

  .cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    row-gap: 10px;
    @media (max-width: 1100px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .card {
    padding: 18px 20px;
    border: 1px solid var(--grey-200);
    border-radius: 8px;
    background-color: var(--base-white);
    transition: background-color 0.3s ease-in;

    // УБРАТЬ ПРИ СДАЧЕ ПРОЕКТА
    &:nth-child(n+13) {
      display: none;
    }

    @include mobile {
      padding: 15px 16px;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: 400;
      font-size: 16px;
      @include mobile {
        font-size: 15px;
      }
      p {
        color: var(--base-black);
        text-decoration: underline;
        display: block;
        display: -webkit-box;
        max-width: 650px;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        @media (max-width: 1100px) {
          max-width: 900px;
        }
        @include mobile {
          max-width: 100%;
          -webkit-line-clamp: 20;
          overflow: visible;
        }
      }
      i {
        font-size: 24px;
        text-decoration: none !important;
        color: var(--grey-500);
        margin-left: 20px;
        transition: color 0.3s ease-in;
        &:hover {
          color: var(--base-grey);
        }
      }
    }
    &:hover {
      background-color: var(--grey-200);
    }
  }

  .disabled {
    pointer-events: none;
    color: var(--grey-200);
    &:hover {
      color: var(--grey-200);
      i {
        color: var(--grey-200);
      }
    }
  }

  span.deleteicon {
    @include medium-tablet {
      width: 100%;
    }
  }
}

// Сортировка

.sort {
  margin-top: 15px;
  @include small-tablet {
    margin-top: 10px;
  }
  @include mobile {
    margin-top: 0;
    margin-bottom: 10px;
  }

  &__list {
    position: relative;
  }
  &__btn {
    border: none;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
    color: var(--base-grey);
    display: inline-flex;
    align-items: center;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.3s ease-in;
    i {
      font-size: 24px;
      margin-left: 20px;
      transition: transform 0.3s ease-in-out, color 0.3s ease-in;
    }
    @include small-tablet {
      font-size: 20px;
      i {
        font-size: 22px;
      }
    }
    @include mobile {
      font-size: 16px;
      i {
        font-size: 18px;
        margin-left: 12px;
      }
    }
  }

  &__btn:hover {
    color: var(--cyan-400);

    i {
      color: var(--cyan-400);
    }
  }

  .dropdown {
    position: absolute;
    width: 320px;
    right: 0;
    padding: 16px 20px;
    border-radius: 8px;
    box-shadow: 0px 24px 24px -16px rgba(0, 0, 0, 0.2);
    background-color: var(--base-white);
    z-index: 1002;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    @include mobile {
      width: 280px;
      left: 0;
      padding: 15px 20px;
    }
  }

  .dropdown--active {
    opacity: 1;
    visibility: visible;
  }

  .dropdown__content {
    max-width: 100%;
    margin: 0 auto;
  }

  .dropdown__list {
    display: flex;
    flex-direction: column;
    align-items: start;
  }

  .dropdown__btn {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
    text-align: left;
    &:not(:last-child) {
      margin-top: 6px;
      margin-bottom: 6px;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}

.search__form--fluid {
  position: relative;
  margin-bottom: 20px;
  @include medium-tablet {
    width: 100%;
  }
  &::before {
    position: absolute;
    content: "\e95c";
    font-family: icons;
    width: 20px;
    height: 20px;
    left: 17px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 18px;
    color: #99A2AD;
    z-index: 50;
  }
  .search__input {
    padding: 18px 40px 18px 40px;
    @media (max-width: 1250px) {
      width: 300px;
    }
    @include medium-tablet {
      width: 100%;
    }
  }
}
