.graph-modal__close {
  right: -38px;
  top: 10px;
  width: 28px;
  height: 28px;
  font-size: 16px;
  line-height: 1;
  text-align: center;
  color: var(--base-white);
  border-radius: 100%;
  background-color: var(--grey-500);

  @include small-tablet {
    right: 15px;
    top: -45px;
  }
  @include mobile {
    right: 20px;
    top: 20px;
  }
  @media (max-width: 370px) {
    right: 15px;
    top: -45px;
  }
}

.graph-modal__container {
  padding: 0;
  max-width: 622px;
}

.graph-modal__content {
  @include mobile {
    .hi__main {
      flex-direction: column;
      justify-content: center;
      .image {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
    .hi__descr {
      font-size: 15px;
    }
  }
  @media (max-width: 370px) {
    font-size: 14px;
  }
  .hi__descr {
    margin-bottom: 0;
  }
}
